<template>
    <div id="my">
        <headerfooter></headerfooter>
        <!-- 顶部 -->
        <div class="header">
            <div class="header-img"><img src="../../../static/images/footer_4.png" alt=""></div>
            <router-link v-if="username == ''" to="/my/login" class="username">登录</router-link>
            <!-- <div @click.stop="showLogin" class="username">登录</div> -->
            <div v-if="username != ''" class="user">{{username}}</div>
        </div>
        <!-- 我的订单 -->
        <!-- <router-view ref="username"></router-view> -->
        <!-- <login v-if="login" @transferUser="getUser"></login> -->
        <div class="my-order">
            <div class="order-top">
                <router-link to="/my/myorder">
                    <span class="order-text">我的订单</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
            <div class="usernav">
                <ul>
                    <!-- <router-link to="/my/myorder"> -->
                    <li>
                        <router-link to="/my/myorder/money">
                            <div class="icon-img"><img src="../../../static/images/usernav_1.png" alt=""></div>
                            <div class="text">代付款</div>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/my/myorder/goods">
                            <div class="icon-img"><img src="../../../static/images/usernav_2.png" alt=""></div>
                            <div class="text">待收货</div>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/my/myorder/comment">
                            <div class="icon-img"><img src="../../../static/images/usernav_3.png" alt=""></div>
                            <div class="text">已收货</div>
                        </router-link>
                    </li>
                    <!-- </router-link> -->
                </ul>
            </div>
        </div>
        <!-- 每一项 -->
        <div class="set">
            <ul>
                <li>
                    <router-link to="/my/detail">
                        <span class="icon"><img src="../../../static/images/my_5.png" alt=""></span>
                        <span class="set-text">修改资料</span>
                        <span class="arrow">></span>
                    </router-link>
                </li>
                <li>
                    <router-link to="/my/pw">
                        <span class="icon"><img src="../../../static/images/my_1.png" alt=""></span>
                        <span class="set-text">修改密码</span>
                        <span class="arrow">></span>
                    </router-link>
                </li>
                <li>
                    <router-link to="/home/service">
                        <span class="icon"><img src="../../../static/images/my_3.png" alt=""></span>
                        <span class="set-text">客服中心</span>
                        <span class="arrow">></span>
                    </router-link>
                </li>
                <li>
                    <router-link to="/my/address">
                        <span class="icon"><img src="../../../static/images/my_2.png" alt=""></span>
                        <span class="set-text">收货地址</span>
                        <span class="arrow">></span>
                    </router-link>
                </li>
                <!-- <li>
                    <span class="icon"><img src="../../../static/images/my_4.png" alt=""></span>
                    <span class="set-text">保养指南</span>
                    <span class="arrow">></span>
                </li> -->
            </ul>
        </div>

        <div v-if="username != ''" @click="layout" class="btn">
            <mt-button type="danger">安全退出</mt-button>
        </div>

        <shade v-if="shade" :warn="warn"></shade>
    </div>
</template>

<script>
import headerfooter from "@/components/header/header.vue";
import shade from "@/components/shade/shade.vue";
export default{
    data()
    {
        return {
            my: [],
            login: false,
            username: '',
            phone: '',
            a: '',
            shade: false,
            warn: ''
        }
    },
    created()
    {
        this.username = window.sessionStorage.getItem(1);
    },
    methods:{
        layout(){
            this.$http.get(this.getIp()+'/mobile.php/logout/logout')
                .then((res) => {
                    this.username = '';
                    console.log('退出成功');
                    // console.log(res);
                    // this.product = res.body.data;
                    // console.log(this.product);
                    if(res.body.code === 200)
                    {
                        // setTimeout(function(){
                        this.warn = '退出成功'    
                        this.shade = true;
                        // },1000)
                    }else if(res.body.code === 205)  
                    {
                        this.warn = '亲，您未登录！！！'
                        this.shade = true;
                    }else if(res.body.code === 300)
                    {
                        this.warn = '缺少参数！！！'
                        this.shade = true;
                    }
            })          
        }
    },
    mounted(){
        // console.log(this.$refs.login.username);
    },
    components: {
        headerfooter,
        shade
    }
}

</script>

<style scoped lang="less" rel="stylesheet/less">
#my {
    padding: 50px 0 70px;
    .header {
        height: 120px;
        background: url(../../../static/images/yhzx.jpg) no-repeat 0 0;
        background-size: 100% 200px;
        padding: 20px 0 0 30px;
        .header-img {
            width: 80px;
            height: 80px;
            border-radius: 50%;
            overflow: hidden;
            display: inline-block;
            vertical-align: middle;
            margin-right: 20px;
            background-color: #fff;
            img {
                width: 100%;
            }
        }
        .username {
            display: inline-block;
            vertical-align: middle;
            font-size: 14px;
            font-weight: 200;
            color: #777;
            border: 1px solid #777;
            padding: 6px 30px;
            border-radius: 5px;
        }
        .user {
            display: inline-block;
            vertical-align: middle;
            font-size: 30px;
            // color: #fff;
        }
    }   

    .my-order {
        margin: 10px 0;
        background-color: #fff;
        .order-top {
            padding: 10px;
            overflow: hidden;
            border-bottom: 1px solid #ddd;
            .order-text {
                float: left;
                color: #666;
            }
            .arrrow {
                float: right;
                color: #666;
            }
        }
        .usernav {
            ul {
                overflow: hidden;
                li {
                    float: left;
                    width: 30%;
                    text-align: center;
                    padding: 10px 0;
                    .icon-img {
                        display: inline-block;
                        width: 30px;
                        height: 30px;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .text {
                        font-size: 14px;
                        color: #333;
                    }
                 }
            }
        }
    }
    .set {
        
        ul {
            li {
                border-bottom: 1px solid #ddd;
                overflow: hidden;
                padding: 10px;
                background-color: #fff;
                &:nth-of-type(2) {
                    margin-bottom: 10px;
                }
                .icon {
                    float: left;
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    overflow: hidden;
                    vertical-align: middle;
                    margin-top: 4px; 
                    img {
                        width: 100%;
                    }
                }
                .set-text {
                    float: left;
                    vertical-align: middle;
                    line-height: 30px;
                    margin-left: 10px;
                    color: #666;
                }
                .arrow {
                    float: right;
                    vertical-align: middle;
                    line-height: 30px;
                    color: #666;
                }
            }
        }
    }
    .btn {
        padding: 20px;
        .mint-button {
            width: 100%;
            background-color: #9e0028;
        }
    }
}

</style>


