<template>
    <div id="draw">
        <v-back :msg="msg"></v-back>
        <div class="tit">安全提现</div>
        <!-- <form action="#"> -->
            <div class="item">
                <label for="username">提现金额￥</label>
                <input v-model.trim="money" id="username" type="text" placeholder="请输入提现金额">
            </div>
            <div class="sub"><input @click="active" class="btn" type="submit" value="提现"></div>
        <!-- </form> -->

        <shade :warn="warn" v-if = "shade"></shade>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default {
    data(){
        return {
            msg: '提现',
            money:'',
            shade: false,
            warn: ''
        }
    },
    methods: {
        active(){
            this.$http.get(this.getIp()+'/mobile.php/Point/cashing?cash='+this.money)
            .then((res) => {
                console.log(res);
                console.log("提现成功");
                console.log(res.body.code);
                if(res.body.code === 200)
                {
                    // setTimeout(function(){
                    this.warn = '提现成功'    
                    this.shade = true;
                    // },1000)
                }else if(res.body.code === 205)  
                {
                    this.warn = '亲，您未登录！！！'
                    this.shade = true;
                }else if(res.body.code === 300)
                {
                    this.warn = '缺少参数！！！'
                    this.shade = true;
                }
                // console.log(this.data);
            })
            
        }
    },
    created()
    {
        // this.$nextTick(function(){
        //     this.$http.get('http://192.168.43.192/mobile.php/Code/attorn')
        //     .then((res) => {
        //         console.log(res);
        //         this.data = res.body.data;
        //         // console.log(this.data[0].number)
        //         console.log(this.data);
        //     })
        // })
        
    },
    components: {
        'v-back':back,
        shade
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#draw {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0px;
    z-index: 99;
    background-color: #efeff4;
    padding: 50px 0;
    form {
        border-radius: 6px;
        overflow: hidden;
    }
    .tit {
        padding: 10px;
        border-bottom: 1px solid #9e0028;
        color: #9e0028;
        margin-bottom: 10px;
    }
    .item {
        background-color: #fff;
        position: relative;
        border-bottom: 1px solid #ddd;
        label {
            position: absolute;
            width: 110px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 60%;
            margin-left: 120px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .sub {
        text-align: center;
        background-color: #fff;
        padding: 10px;
        .btn {
            background-color: #9e0028;
            padding: 4px 30px;
            border: none;
            color: #fff;
            font-weight: 700;
            border-radius: 6px;
        }

    }

}
</style>



