<template>
            
            <div class="code">
                <div class="codeImg">
                    <img src="../../../static/images/btn-ewm.png" alt="二维码"/>
                    <span @click.stop="back">关闭</span>
                </div>
            </div>  
</template>
<script>
import back from '@/components/back/back.vue';

export default {
    data(){
        return {
            msg: '客服微信'
        }
    },
    methods: {
        back(){
            this.$router.go(-1);
        }
    },
    components: {
        'v-back':back
    }
}
</script>
<style scpoed lang="less" rel="stylesheet">
.code{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #fff;
    z-index: 19;
    .codeImg {
        position: absolute;
        width: 80%;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: right;
        img {
            width: 100%;
        }
    }
}
</style>



