<template>
        <!-- 返回功能 -->
            <div class="register-header">
                <div class="tit">{{msg}}</div>
                <a class="back" href="javascript:void(0)"  @click="back">
                    <img src="../../../static/images/icon-back.png" alt="">
                </a>
            </div>  
</template>
<script>
export default {
    props: ['msg'],
    methods: {
        back(){
            this.$router.go(-1);
        }
    }
}
</script>
<style scoped lang="less" rel="stylesheet">
        .register-header {
            height: 50px;
            background-color: #9e0028;
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            z-index: 99;
            .tit {
                text-align: center;
                line-height: 50px;
                color: #fff;
            }
            .back {
                position: absolute;
                width: 10px;
                height: 20px;
                left: 20px;
                top: 15px;
                img {
                    width: 100%;
                }
            }
        }
</style>



