<template>
    <div id="activation">
        <v-back :msg="msg"></v-back>
        <div class="tit">激活前确认您已到对方款项</div>
        <!-- <form action="#"> -->
            <div class="item">
                <label for="username">激活账户</label>
                <input v-model="phone" id="username" type="text" placeholder="请输入激活账户">
            </div>
            <div class="item">
                <label for="area">二级密码</label>
                <input v-model="pin" id="area" type="text" placeholder="请输入二级密码">
            </div>
            <div class="item">
                <label for="addr">激活码剩余</label>
                <input v-model="a" id="addr" type="text" placeholder="激活码剩余" disabled>
            </div>
            <div class="sub"><input @click="active" class="btn" type="submit" value="激活"></div>
        <!-- </form> -->
            <shade v-if="shade" :warn="warn"></shade>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default {
    data(){
        return {
            msg: '会员激活',
            phone:'15938785986',
            pin: '1112',
            data: [],
            a:'',
            shade: false,
            warn: ''
        }
    },
    methods: {
        active(){
            this.$http.get(this.getIp()+'/mobile.php/Code/activ?phone='+this.phone+'&pin='+this.pin)
            .then((res) => {
                console.log(res);
                // this.data = res.body.data;
                // console.log(this.data);
                if(res.body.code === 200)
                {
                    // setTimeout(function(){
                    this.warn = '会员激活成功'    
                    this.shade = true;
                    this.$router.go(-1);
                    // },1000)
                }else if(res.body.code === 205)  
                {
                    this.warn = '亲，您未登录！！！'
                    this.shade = true;
                }else if(res.body.code === 300)
                {
                    this.warn = '缺少参数！！！'
                    this.shade = true;
                }
            })
            // this.shade = true;
        }
    },
    created()
    {
        this.$nextTick(function(){
            this.$http.get(this.getIp()+'/mobile.php/Code/attorn')
            .then((res) => {
                console.log(res);
                this.data = res.body.data;
                // console.log(this.data[0].number)
                console.log(this.data);
                if(this.data.length >0)
                {
                    this.a = this.data[0].code_no;
                }
            })
        })
        
    },
    components: {
        'v-back':back,
        shade
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#activation {
    padding-top: 50px;
    form {
        border-radius: 6px;
        overflow: hidden;
    }
    .tit {
        padding: 10px;
        border-bottom: 1px solid #9e0028;
        color: #9e0028;
        margin-bottom: 10px;
    }
    .item {
        background-color: #fff;
        position: relative;
        border-bottom: 1px solid #ddd;
        label {
            position: absolute;
            width: 110px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 60%;
            margin-left: 120px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .sub {
        text-align: center;
        background-color: #fff;
        padding: 10px;
        .btn {
            background-color: #9e0028;
            padding: 4px 30px;
            border: none;
            color: #fff;
            font-weight: 700;
            border-radius: 6px;
        }

    }

}
</style>



