<template>
    <div id="shop">
        <v-back :msg="msg"></v-back>
        <div class="tit">请联系您的推荐人进行购买</div>
        <form action="#">
            <div class="item">
                <label for="username">推荐人账号</label>
                <input v-model="data.phone" id="username" type="text" placeholder="请输入推荐人账号">
            </div>
            <div class="item">
                <label for="tel">推荐人支付宝</label>
                <input v-model="data.alipay" id="tel" type="tel" placeholder="请输入推荐人支付宝">
            </div>
            <div class="item">
                <label for="area">推荐人微信号</label>
                <input v-model="data.wechat" id="area" type="text" placeholder="请输入推荐人微信号">
            </div>
            <div class="item">
                <label for="addr">当前剩余</label>
                <input v-model="data.code_no" id="addr" type="text" placeholder="请输入新密码" value="9" disabled>
            </div>
            <!-- <div class="sub"><input class="btn" type="submit" value="购买"></div> -->
        </form>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg: '激活码购买',
            phone: '',
            alipay: '',
            wechat: '',
            code_no:'',
            data: {}
        }
    },
    methods: {

    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/Code/buy')
            .then((res) => {
                console.log(res);
                this.data = res.body.data;
                // console.log(this.data);
        })
    },
    components: {
        'v-back':back
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#shop {
    padding-top: 50px;
    form {
        border-radius: 6px;
        overflow: hidden;
    }
    .tit {
        padding: 10px;
        border-bottom: 1px solid #9e0028;
        color: #9e0028;
        margin-bottom: 10px;
    }
    .item {
        background-color: #fff;
        position: relative;
        border-bottom: 1px solid #ddd;
        label {
            position: absolute;
            width: 110px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 60%;
            margin-left: 120px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .sub {
        text-align: center;
        background-color: #fff;
        padding: 10px;
        .btn {
            background-color: #9e0028;
            padding: 4px 30px;
            border: none;
            color: #fff;
            font-weight: 700;
            border-radius: 6px;
        }

    }
}
</style>



