<template>
        <div class="user">
            <v-back :msg='msg'></v-back>
            <div class="list">
                <ul>
                    <li>
                        <span class="uname">用户名</span>
                        <span class="tname">真实姓名</span>
                        <span class="status">状态</span>
                    </li>
                    <li v-for="item in data">
                        <span class="uname">{{item.name}}</span>
                        <span class="tname">{{item.username}}</span>
                        <span :class="[status,{'red':item.status==0}]">{{item.status == 0? '激活':'正常'}}</span>
                    </li>
                </ul>
            </div>
        </div>  
</template>
<script>
import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg: '直推用户',
            data: []
        }
    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/Direct/index')
            .then((res) => {
                console.log(res);
                this.data = res.body.data;
                // console.log(this.data);
        })
    },
    components: {
        'v-back': back
    }
}
</script>
<style scoped lang="less" rel="stylesheet">
// 直推注册
.user {
        // position: absolute;
        // width: 100%;
        // height: 100%;
        // left: 0;
        // top: 50px;
        // z-index: 29;
        // background-color: #efeff4;
        padding-top: 50px;
        .list {
            padding-top: 20px;
            ul {
                background-color: #fff;
                border-radius: 6px;
                li {
                    overflow: hidden;
                    line-height: 40px;
                    background-color: #E5E5E5;
                    border-bottom: 1px solid #ddd;
                    &:first-of-type {
                        background-color: #F1F1F1;
                        span {
                            font-weight: 700;
                            color: #666;
                            &.status {
                                color: #666;
                            }
                        }
                    }
                    span {
                        float: left;
                        text-align: center;
                        color: #666;
                        height: 40px;
                        &.uname {
                            width: 40%;
                        }
                        &.tname {
                            width: 30%;
                        }
                        &.status {
                            width: 30%;
                            color: #0099FF;
                        }
                        &.red {
                            color: #9e0028;
                        }
                    }
                }
            }
        }
}
</style>



