import Vue from 'vue';
import Router from 'vue-router';
import home from '@/components/Home.vue';
import auction from '@/components/auction/auction.vue';
import integral from '@/components/auction/integral.vue';
import activate from '@/components/activate/activate.vue';
import activation from '@/components/activate/activation.vue';
import widthdraw from '@/components/auction/widthdraw.vue';
import thaw from '@/components/auction/thaw.vue';
import attorn from '@/components/activate/attorn.vue';
import shop from '@/components/activate/shop.vue';
import car from '@/components/car/car.vue';
import my from '@/components/my/my.vue';
import login from '@/components/my/login.vue';
import register from '@/components/recommend/register.vue';
import service from '@/components/service/service.vue';
// import product from '@/components/product/product.vue';
import notice from '@/components/notice/notice.vue';
import logistics from '@/components/logistics/logistics.vue';
import address from '@/components/my/address.vue';
import myorder from '@/components/myorder/myorder.vue';
import goods from '@/components/myorder/goods.vue';
import money from '@/components/myorder/money.vue';
import comment from '@/components/myorder/comment.vue';
import team from '@/components/team/team.vue';
import recommend from '@/components/recommend/recommend.vue';
import detail from '@/components/my/detail.vue';
import pw from '@/components/my/pw.vue';
import user from '@/components/recommend/user.vue';


Vue.use(Router)

export default new Router({
  linkActiveClass: 'mui-active',
  routes: [
    {path:'/',redirect:'/home'},
    {path:'/home',component: home},
    {path:'/activate',component: activate},
    {path:'/activate/shop',component: shop},
    {path:'/activate/attorn',component: attorn},
    {path:'/activate/activation',component: activation},
    {path:'/car',component: car},
    {path:'/my',component: my},
    {path:'/my/login',component: login},
    // {path:'/my',component: my,
    //   children:[
    //     {path:'login',component: login}
    //   ]
    // },
    {path:'/home/recommend/register',component: register},
    {path:'/home/service',component: service},
    // {path:'/home/product',component: product},
    {path:'/home/notice',component: notice},
    {path:'/home/auction',component: auction},
    {path:'/home/auction/integral',component: integral},
    {path:'/home/auction/widthdraw',component: widthdraw},
    {path:'/home/auction/thaw',component: thaw},
    {path:'/my/logistics',component: logistics},
    {path:'/my/address',component: address},
    {
      path:'/my/myorder',
      component: myorder,
      children: [
        {path: 'money',component: money},
        {path: 'goods',component: goods},
        {path: 'comment',component: comment}
      ]
    },
    // {path:'/my/myorder/goods',component: goods},
    {path:'/home/team',component: team},
    {path:'/home/recommend',component: recommend},
    {path:'/my/detail',component: detail},
    {path:'/my/pw',component: pw},
    {path:'/home/recommend/user',component: user}

    
  ]
})



