<template>
<div id="notice">
    <v-back :msg='msg'></v-back>
    <div v-html="notice[0].content" class="content"></div>
    
</div>
</template>

<script>
import back from '@/components/back/back.vue';

export default{
    props:['notice'],
    data()
    {
        return {
            msg:'公告',
            notice: []
        }
    }
    ,
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/Picture/index')
            .then((res) => {
                console.log(res);
                // this.slide = res.body.data.picture;
                this.notice = res.body.data.notice;
                // console.log(this.notice);

        })
    },
    methods:{

    },
    components:{
        'v-back': back
    }
}

</script>

<style scoped lang="less" rel="stylesheet">
#notice {
    padding-top: 60px;
    .content {
        width: 100%;
        height: 200px;
    }
}

</style>


