<template>
        <div class="login">
            <div class="login-header">
                <img class="logo" src="../../../static/images/logo.png" alt="">
                <a class="back" href="javascript:void(0)" @click="back">
                    <img src="../../../static/images/icon-back.png" alt="">
                </a>
            </div>
            <div class="login-box">
                <!-- <form action="#" class="form"> -->
                    <div class="username">
                        <input v-model="uname" type="text" id="username" placeholder="手机/邮箱">
                    </div>
                    <div class="password">
                        <input v-model="pw" type="password" id="password" placeholder="登录密码">
                    </div>
                    <input @click.stop="userLogin" class="btn" type="submit" value="登录"> 
                <!-- </form> -->
            </div>
            <shade v-if="shade" :warn="warn"></shade>
        </div>  
</template>
<script>
import shade from '@/components/shade/shade.vue';
let user = 1;
export default {
    data(){
        return {
            uname: '13676908045',
            pw: '123456',
            username: '',
            shade: false,
            warn: ''
        }
    }, 
    methods: {
        back(){
            this.$router.go(-1);
        },
        userLogin(){
            this.$http.get(this.getIp()+'/mobile.php/login/index?phone='+this.uname+'&password='+this.pw)
            .then((res) => {
                console.log(res);
                if(res.body.code == 200)
                {
                    this.username = res.body.data.username;
                    this.a = res.body.data.asd;
                    // console.log(this.username);
                    console.log("登录成功");
                    this.$router.go(-1);
                    if(res.body.code === 200)
                    {
                        // setTimeout(function(){
                        this.warn = '登录成功'    
                        this.shade = true;
                        // },1000)
                    }else if(res.body.code === 205)  
                    {
                        this.warn = '亲，您未登录！！！'
                        this.shade = true;
                    }else if(res.body.code === 300)
                    {
                        this.warn = '缺少参数！！！'
                        this.shade = true;
                    }
                }else {
                    console.log("登陆失败！！！");
                }
                window.sessionStorage.setItem(user,this.username);
                
            },(res)=>{
                // console.log(res);
                console.log("登陆失败！！！");
            })      
        },
        inputEnd(){

            // this.$http.get('http://192.168.43.192/mobile.php/login/index',{
            //     phone:username,
            //     name:password
            // })
            // .then((res) => {
            //     console.log(res);
            //     //    this.my = res.body.data;
            //     // console.log(this.my);
            // })        
        }
    },
    created()
    {
        
    },
    components:{
        shade
    }
}
</script>
<style scoped lang="less" rel="stylesheet/less">
// 登录页面
    .login {
        position: fixed;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 29;
        background-color: #efeff4;
        .login-header {
            height: 200px;
            background-color: #9e0028;
            padding-top: 60px;
            position: relative;
            .logo {
                height: 80px;
                width: 100%;
            }
            .back {
                position: absolute;
                width: 10px;
                height: 20px;
                left: 20px;
                top: 20px;
                img {
                    width: 100%;
                }
            }
        }
        .login-box {
            padding: 20px;
            // .form {
                input#username,input#password {
                    border-radius: 20px;
                    border: none;
                     padding-left: 40px;
                    background: #fff url(../../../static/images/login_1.png) no-repeat 5px 5px;
                    background-size: 30px 30px;
                } 
                input#password {
                    background-image: url(../../../static/images/login_2.jpg);
                }
                .btn {
                    width: 100%;
                    border-radius: 20px;
                    background-color: #9e0028;
                    border: none;
                    font-size: 20px;
                }
            // }
        }
    }
</style>



