<template>
    <div id="product">
        <v-back :msg='msg'></v-back>
        <!-- 产品轮播 -->
        <div class="swipe">
            <mt-swipe>
                <mt-swipe-item>
                    <img src="../../../static/images/product_1.jpg" alt=""/>
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../../static/images/product_2.jpg" alt=""/>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <!-- 商品介绍 -->
        <div class="introduce">
            <div class="tit">{{selected.title}}</div>
            <div class="tip">推广价：<span class="red">{{selected.product_price | price}}</span></div>
            <!-- <div class="del">吊牌价：<del>￥{{selected.product_price}}</del></div> -->
            <!-- <div class="total">全网月销：18件</div> -->
        </div>
        <div class="detail">
            <!-- <div class="tit">
                <span class="icon"><img src="../../../static/images/usernav_3.png" alt=""></span>
                <span class="text">详细参数</span>
            </div> -->
            <!-- <div class="list">
                <ul>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                    <li><span>模号：</span>234324234</li>
                </ul>
            </div> -->
            <div class="img">
                <img :src="selected.picture" alt="图片找不到">
            </div>
            <div class="img">
                <img src="../../../static/images/product_2.jpg" alt="图片找不到">
            </div>
            <div class="img">
                <img src="../../../static/images/product_3.jpg" alt="图片找不到">
            </div>
            <div class="img">
                <img src="../../../static/images/product_4.jpg" alt="图片找不到">
            </div>
            <div class="img">
                <img src="../../../static/images/product_5.jpg" alt="图片找不到">
            </div>
            <div class="img">
                <img src="../../../static/images/product_6.jpg" alt="图片找不到">
            </div>
        </div>

        <!-- 购物车 -->
        <div class="car">
            <router-link class="fl" to="/car">
                <div class="img">
                    <img src="../../../static/images/footer_3.png" alt="">
                    <p>购物车</p>
                </div>
            </router-link>
            <div class="fr">
                <div @click.stop="buy" class="buy">立刻购买</div>
            </div>
            <div class="fr">
                <div @click.stop="join" class="join">加入购物车</div>
                <!-- @click.stop.prevent="addFirst" -->
            </div>
        </div>

        <shade v-if="shade" :warn="warn"></shade>

    </div>
</template>

<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default{
        props:['selected'],
        data()
        {
            return {
                msg: '商品详情',
                id: '',
                price: '',
                product: '',
                shade: false,
                warn: ''
            }
        },
        created()
        {
            
        },
        methods:{
            join(){
                this.$nextTick(function(){
                    this.$http.get(this.getIp()+'/mobile.php/cart/add?pid='+this.selected.id)
                        .then((res) => {
                            console.log(res);
                            console.log('加入购物车成功');
                            this.$router.push({path:'/car'});
                            // console.log(this.selected);
                            if(res.body.code === 200)
                            {
                                // setTimeout(function(){
                                this.warn = '加入购物车成功'    
                                this.shade = true;
                                // },1000)
                            }else if(res.body.code === 205)  
                            {
                                this.warn = '亲，您未登录！！！'
                                this.shade = true;
                            }else if(res.body.code === 300)
                            {
                                this.warn = '缺少参数！！！'
                                this.shade = true;
                            }
                            
                    })
                }) 
                // this.warn = '加入购物车成功';
                // this.shade = true;               
            },
            buy(){
                this.$nextTick(function(){
                    this.$http.get(this.getIp()+'/mobile.php/Order/order?pid='+this.selected.id+'&total='+this.selected.product_price)
                        .then((res) => {
                            console.log(res);
                            console.log('购买成功');
                            
                            // console.log(this.selected);
                            if(res.body.code === 200)
                            {
                                this.warn = '购买成功'    
                                this.shade = true;
                                setTimeout(function(){
                                    this.$router.push({path:'/my/myorder/money'});
                                },1000)
                            }else if(res.body.code === 205)  
                            {
                                this.warn = '亲，您未登录！！！'
                                this.shade = true;
                            }else if(res.body.code === 300)
                            {
                                this.warn = '缺少参数！！！'
                                this.shade = true;
                            }
                            
                    })
                })
                // this.warn = '购买成功'  
                // this.shade = true;              
            }
        },
        components: {
            'v-back': back,
            shade
        }
    }
</script>

<style scoped lang="less" rel="stylesheet/less">
#product {
    position: absolute;
    padding-bottom: 100px;
    width: 100%;
    left: 0;
    top: 50px;
    background-color: #fff;
    z-index: 40;
    .swipe {
        height: 260px;
        img {
            width: 100%;
            height: 100%;

        }
    }
    .introduce {
        padding: 10px;
        position: relative;
        border-bottom: 1px solid #ddd;
        .tit {
            font-size: 14px;
        }
        .tip {
            font-size: 12px;
            color: #666;
            .red {
                color: #9e0028;
            }
        }
        .del {
            color: #666;
            font-size: 12px;
        }
        .total {
            position: absolute;
            right: 20px;
            bottom: 10px;
            font-size: 12px;
        }
    }
    .detail {
        .tit {
            line-height: 40px;
            height: 40px;
            padding-left: 10px;
            .icon {
                display: inline-block;
                width: 18px;
                height: 18px;
                // margin-top: 3px;
                img {
                    width: 100%;
                }
            }
            .text {

            }
        }
        .list {
            padding: 0 10px;
            ul {
                li {
                    float: left;
                    width: 50%;
                }
            }
        }
        .img {
            padding: 10px;
            img {
                width: 100%;
            }
        }
    }

    .car {
        position: fixed;
        width: 100%;
        height: 60px;
        left: 0;
        bottom: 0;
        background-color: #fff;
        border-top: 1px solid #ddd;
        .fl {
            width: 20%;
            height: 100%;
            border-right: 1px solid #ddd;
            .img {
                width: 100%;
                text-align: center;
                img {
                    width: 30px;
                    height: 30px;
                }
                p {
                    margin: 0;
                }
            }
        }
        .fr {
            width: 30%;
            .join,.buy {
                width: 100%;
                line-height: 60px;
                text-align: center;
                background-color: #ffb03f;
                color: #fff;
            }
            .buy {
                background-color: #9e0028;
            }
        }
    }
}
</style>



