<template>
    <div id="goods">
        <div class="box">
            <div v-if="goodsList.length > 0" v-for="item in goodsList" class="item">
                <div class="num">
                    <div class="left">订单号：{{item.order_number}}</div>
                    <!-- <div class="right">已经收货</div> -->
                </div>
                <div class="product">
			        <div class="img-text">
                        <img :src="item.picture" alt="">
                        <div class="content">
                            <p class="text">{{item.title}}</p>
                            <p class="text">{{item.short_title}}</p>
                            <div class="price">{{item.product_price | price}}</div>
                            <div class="total">合计：{{item.total | price}}</div>
                        </div>
                    </div>      
                </div>
                <div class="btn">
                    <button>查看物流</button>
                    <button @click.stop="goods(item.order_number)">确认收货</button>
                </div>
            </div>
        </div>
        <div v-if="goodsList == null" class="empty">待收货页面是空的</div>
    </div>  
</template>
<script>
export default {
    props:['goodsList'],
    data(){
        return {
            msg: '待收货'
        }
    },
    created()
    {
        
    },
    methods: {
        
        goods(id){
            this.$nextTick(function(){
            this.$http.get(this.getIp()+'/mobile.php/Order/take?order_number='+id)
            .then((res) => {
                console.log(res);
                // this.moneyList = res.body.data;
                // console.log(this.data);
            })
            })
        }
        
    },
    components: {
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#goods {
    padding-bottom: 100px;
    .box {
        margin-top: 10px;
        .item {
            margin-bottom: 10px;
            background-color: #fff;
            border-bottom: 1px solid #9e0028;
            .num {
                overflow: hidden;
                height: 40px;
                line-height: 40px;
                padding: 0 10px;
                border-top: 1px solid #9e0028;
                border-bottom: 1px solid #9e0028;
                .left {
                    float: left;
                    font-size: 14px;
                }
                .right {
                    float: right;
                    color: #259B23;
                    font-size: 14px;
                }
            }
            .product {
                border-bottom: 1px solid #9e0028;
                .img-text {
                    padding: 10px;
                    overflow: hidden;
                    img {
                        float: left;
                        width: 80px;
                        height: 80px;
                        margin-right: 10px;
                        border: 1px solid #9e0028;
                        border-radius: 5px;
                    }
                    .content {
                        padding-left: 10px;
                        position: relative;
                        .text {
                            font-size: 16px;
                        }
                        .price {
                            margin-bottom: 10px;
                            color: #9e0028;
                        }
                        .total {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            color: #666;
                        }
                    }
                }
            }
            .btn {
                text-align: right;
                padding: 10px;
                button {
                    border-radius: 8px;
                    border-color: #333;
                    &:last-of-type {
                        border-color: #259B23;
                        color: #259B23;
                        margin-left: 10px;
                    }
                }
            }
        }
    }
    .empty {
        text-align: center;
        color: #9e0028;
    }

}
</style>



