<template>
<!-- 直推注册 -->
        <div class="register">
            <v-back :msg='msg'></v-back>
            <div class="register-box">
                <!-- <form action="#" class="form"> -->
                    <div class="username">
                        <input v-model="phone" type="text" id="username" placeholder="手机/邮箱">
                    </div>
                    <div class="password">
                        <input v-model="pw" type="password" id="password" placeholder="注册密码">
                    </div>
                    <input @click.stop="register" class="btn" type="submit" value="注册"> 
                <!-- </form> -->
            </div>
            <shade v-if="shade" :warn="warn"></shade>
        </div>  
</template>
<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default {
    data(){
        return {
            msg: '添加会员',
            phone: '15938785986',
            pw: '1234556',
            shade: false,
            warn: ''
        }
    },
    methods: {
        register(){
            this.$http.get(this.getIp()+'/mobile.php/member/add?phone='+this.phone+'&password='+this.pw)
                .then((res) => {
                    console.log(res);
                    this.$router.go(-2);
                    console.log("添加成功");
                    //    this.my = res.body.data;
                    // console.log(this.my);
                    if(res.body.code === 200)
                    {
                        // setTimeout(function(){
                        this.warn = '注册成功'    
                        this.shade = true;
                        // },1000)
                    }else if(res.body.code === 205)  
                    {
                        this.warn = '亲，您未登录！！！'
                        this.shade = true;
                    }else if(res.body.code === 300)
                    {
                        this.warn = '缺少参数！！！'
                        this.shade = true;
                    }
            })
            // this.shade = true;
        }
    },
    created()
    {
        
    },
    components: {
        'v-back': back,
        shade
    }
}
</script>
<style scoped lang="less" rel="stylesheet">
// 直推注册
.register {
        // position: absolute;
        // width: 100%;
        // height: 100%;
        // left: 0;
        // top: 50px;
        // z-index: 29;
        // background-color: #efeff4;
        padding-top: 50px;
        .register-box {
            padding: 20px;
            // .form {
                input#username,input#password {
                    border-radius: 20px;
                    border: none;
                     padding-left: 40px;
                    background: #fff url(../../../static/images/login_1.png) no-repeat 5px 5px;
                    background-size: 30px 30px;
                } 
                input#password {
                    background-image: url(../../../static/images/login_2.jpg);
                }
                .btn {
                    width: 100%;
                    border-radius: 20px;
                    background-color: #9e0028;
                    border: none;
                    font-size: 20px;
                }
            // }
        }
}
</style>



