<template>
<div id="logistics">
    <v-back :msg="msg"></v-back>
    <div class="details">
        <div class="top">
            <div class="num">货运单号： <span>2544455555554545</span></div>
            <div class="company">物流公司： <span>圆通快递</span></div>
            <div class="phone">客服电话： <span class="green">120110332</span></div>
        </div>
        <div class="bottom">
            <div class="item">
                <div class="tit">舒服撒范德萨发顺丰撒范德萨发顺丰胜多负少的</div>
                <div class="time">2017-05-16 16:20:34</div>
            </div>
            <div class="item">
                <div class="tit">舒服撒范德萨发顺丰撒范德萨发顺丰胜多负少的</div>
                <div class="time">2017-05-16 16:20:34</div>
            </div>
            <div class="item">
                <div class="tit">舒服撒范德萨发顺丰撒范德萨发顺丰胜多负少的</div>
                <div class="time">2017-05-16 16:20:34</div>
            </div>
        </div>
    </div>
</div>  
</template>
<script>
import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg: '查看物流'
        }
    },
    methods: {

    },
    components: {
        'v-back':back
    }
}
</script>
<style scoped lang="less" rel="stylesheet">
#logistics {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 50px;
    z-index: 29;
    background-color: #efeff4;
    .details {
        .top {
            background-color: #fff;
            padding: 10px 20px;
            line-height: 28px;
            color: #888;
            span {
                color: #000;
                &.green {
                    color: #3EA33D;
                }
            }
        }
        .bottom {
            background-color: #fff;
            margin-top: 10px;
            padding: 0 20px;
            .item {
                margin-left: 30px;
                padding: 10px 0;
                border-top: 1px solid #999;
                line-height: 26px;
                color: #999;
                position: relative;
                &::before {
                    content: '';
                    position: absolute;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background-color: #999;
                    left: -20px;
                    top: 40%;
                }
                &:first-of-type {
                    border:none;
                    &::before {
                        width: 8px;
                        height: 8px;
                        left: -24px;
                        background-color: #3EA33D;
                        border: 3px solid #90CC8F;
                        
                    }
                    .tit {
                         color: #3EA33D;
                    }
                }
                
            }
        }
    }
}
</style>



