<template>
    <div id="shade">
        <div class="prompt">
            <div class="tit">
                {{warn}}
            </div>
            <div class="btn">
                <mt-button @click="shadeshow" type="danger">确定</mt-button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ['warn'],    
    data(){
        return {
            msg: '',
            show: true
        }
    },
    methods: {
        shadeshow(){
            this.$parent.shade = false;
        }
    },
    created()
    {
           
    },
    components: {
        
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#shade {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; 
    background-color: rgba(0,0,0,.8);
    z-index: 299;
    .prompt {
        position: absolute;
        width: 280px;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        background-color: #fff;
        border-radius: 20px;
        text-align: center;
        .tit {
            height: 80px;
            line-height: 80px;
            border-bottom: 1px solid #ddd;
        }
        .btn {
            padding: 20px;
            .mint-button {
                background-color: #9e0028;
                width: 200px;
            }
        }
    }
}
</style>



