<template>
    <div id="home">
        <headerfooter></headerfooter>

        <!--轮播图-->
        <div class="swipe">
            <mt-swipe :show-indicators="false">
                <mt-swipe-item v-for="item in slide">
                    <img :src="item.picture" alt=""/>
                </mt-swipe-item>
                <!-- <mt-swipe-item>
                    <img src="../../static/images/slide_1.jpg" alt=""/>
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../static/images/slide_2.jpg" alt=""/>
                </mt-swipe-item> -->
            </mt-swipe>
        </div>


        <!--导航-->
        <div class="grid">
            <div class="mui-content">
                <ul class="mui-table-view mui-grid-view mui-grid-9">
                    <li class="mui-table-view-cell mui-media mui-col-xs-3">
                        <router-link to="/home/auction">
                            <span class="icon icon-1"><img src="../../static/images/nav_1.png" alt=""></span>
                            <div class="mui-media-body">财务</div>
                        </router-link>
                    </li>
                    <li class="mui-table-view-cell mui-media mui-col-xs-3"><router-link to="/home/recommend">
                        <span class="icon icon-2"><img src="../../static/images/nav_2.png" alt=""></span>
                        <div class="mui-media-body">直推</div></router-link></li>
                    <li class="mui-table-view-cell mui-media mui-col-xs-3"><router-link to="/home/team">
                        <span class="icon icon-3"><img src="../../static/images/nav_3.png" alt=""></span>
                        <div class="mui-media-body">团队</div></router-link></li>
                    <li class="mui-table-view-cell mui-media mui-col-xs-3"><router-link to="/home/service">
                        <span class="icon icon-4"><img src="../../static/images/nav_4.png" alt=""></span>
                        <div class="mui-media-body">客服</div></router-link></li>
                </ul>
            </div>

        </div>

        <!-- 公告 -->
        <!-- <div class="header_notice">
            <router-link to="/home/notice">
                <span></span>
                <p class="notice_text">{{tit}}</p>
                <span> > </span>
            </router-link>
        </div> -->
      
        <!--产品展示-->
        <div class="product">
            <div class="life">
                <ul class="mui-table-view mui-grid-view">
                    <li @click.stop="select(food.id,$event)" v-for="food in product" :key="food.id" class="li mui-table-view-cell mui-media mui-col-xs-6">
                        <!-- <router-link to="/home/product"> -->
                            <img class="img mui-media-object" :src="food.picture" alt="图片找不到">
                            <div class="tit mui-media-body">{{food.title}}</div>
                            <div class="price">{{food.product_price | price }}</div>
                        <!-- </router-link> -->
                    </li>
                    
                </ul>
            </div>
        </div>

        <product v-if="show" :selected="selected"></product>

        <!-- <div @click.stop = "showshade" class="btn">shade</div> -->
        <!-- <shade v-if = "shade"></shade> -->
    </div>
</template>

<script type="text/ecmascript-6">
import product from '@/components/product/product.vue';
import headerfooter from '@/components/header/header.vue';
import shade from '@/components/shade/shade.vue';
    export default{
        name:'home',
        data()
        {
            return {
                show: false,
                slide: [],
                product: [],
                selected: {},
                id: '',
                tit: '',
                shade: false
            }
        },
        created()
        {
            this.$http.get(this.getIp()+'/mobile.php/Picture/index')
                .then((res) => {
                    console.log(res);
                    this.slide = res.body.data.picture;
                    this.tit = res.body.data.notice[0].full_title;
                    // console.log(this.notice);
            }),
            this.$http.get(this.getIp()+'/mobile.php/product/index')
                .then((res) => {
                    console.log(res);
                    this.product = res.body.data;
                    // console.log(this.product);
            })
        },
        methods:{
            showshade(){
                this.shade = true;
            },
            // selectFood(food,event)
            // {
            //     if(!event._constructed)
            //     {
            //         return;
            //     }
            //     this.selectedFood = food;
            //     this.$refs.food.show();
            //     console.log(this.selectedFood);
            // }
            select(id,event)
            {
                this.$http.get(this.getIp()+'/mobile.php/Product/proinfo?pid='+id)
                .then((res) => {
                    // console.log(res);
                    this.selected = res.body.data;
                    // console.log(this.product);
                }) 
                this.show = true;           
            }
        },
        components: {
            product,
            headerfooter,
            shade
        }
    }

</script>

<style code scoped lang="less" rel="stylesheet/less">
.btn {
    position: fixed;
    // width: 20px;
    // height: 20px;
    left: 0;
    top: 0;
    border: 1px solid #ccc;
    padding: 5px 10px;
    z-index: 199;
}
#home {
    padding: 50px 0 70px;
}
// 导航九宫格
.mui-grid-view.mui-grid-9{
    background-color: #f8f8f8;
    border: none;
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell {
    border: none;
}
.icon{
    display: inline-block;
    width: 40px;
    height: 40px;
    img {
        width: 100%;
    }
}
.mui-table-view.mui-grid-view .mui-table-view-cell .mui-media-body {
    margin-top: 0;
}
// 公告
.header_notice {
        height: 36px;
        position: relative;
        padding: 0 30px 0 40px;
        color: rgb(255,255,255);
        border-top: 1px solid #c8c7cc;
        border-bottom: 1px solid #c8c7cc;
        .notice_text {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-size: 10px;
            line-height: 36px;
        }
        span {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            &:first-of-type {
                width: 30px;
                height: 20px;
                background: url(../../static/images/bulletin@2x.png) no-repeat 0 0;
                background-size: 30px 20px;
                left: 8px;
                border-radius: 2px;
            }
            &:last-of-type {
                font-size: 20px;
                right: 10px;
                // width: 30px;
                color: black;
            }
        }
}
// 产品
.product {
    background-color: #fff;
    // margin-top: 10px;
}

#home {
    //轮播图
    .swipe {
        height: 170px;
        .mint-swipe {
            .mint-swipe-items-wrap {
                .mint-swipe-item {
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
    //导航九宫格
    .grid {
        // padding-top: 15px;
        padding-bottom: 10px;
        background-color: #f8f8f8;
        // .item {
        //     position: fixed;
        //     width: 100%;
        //     height: 100%;
        //     left: 0;
        //     top: 0;
        //     background-color: #fff;
        //     z-index: 19;
        // }
        .mui-content > .mui-table-view:first-child {
            margin-top: 0;
            .mui-table-view-cell {
                padding: 0;
            }
        }
    }
}

//产品
.mui-table-view.mui-grid-view {
    // background-color: #f8f8f8;
    .li {
        background-color: #fff;
        margin-right: 0;
        .img {
            height: 100px;
            border: 1px solid #ddd;
        }
        .tit {
            text-align: left;
            line-height: 20px;
            height: 20px;
        }
        .price {
            text-align: left;
            color: #9e0028;
        }
    }
}
.mui-table-view:after {
    height: 0;
}
</style>


