import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg:'团队',
            username: '',
            team111: true,
            team11: true,
            team211: true,
            team21: true,
            team311: true,
            team31: true,
            team411: true,
            team41: true,
            team511: true,
            team51: true,
            data: []
        }
    },
    methods: {

    },
    created()
    {
        this.username = window.sessionStorage.getItem(1);
        this.$http.get(this.getIp()+'/mobile.php/Direct/team')
            .then((res) => {
                console.log(res);
                this.data = res.body.data;
                // console.log(this.data);
