<template>
    <div id="pw">
        <v-back :msg="msg"></v-back>
        <div class="tit">修改登录密码</div>
        <!-- <form action="#"> -->
            <div class="item">
                <label for="tel">原密码</label>
                <input v-model="password" id="tel" type="tel" placeholder="请输入原始密码">
            </div>
            <div class="item">
                <label for="area">新密码</label>
                <input v-model="newpassword" id="area" type="text" placeholder="请输入新新密">
            </div>
            <div class="item">
                <label for="addr">确认密码</label>
                <input v-model="repassword" id="addr" type="text" placeholder="确认并输入密码">
            </div>
            <div class="sub"><input @click.stop="savepw" class="btn" type="submit" value="修改"></div>
        <!-- </form> -->
        <div class="tit">修改二级密码</div>
        <!-- <form action="#"> -->
            <div class="item">
                <label for="tel">原密码</label>
                <input v-model="pin" id="tel" type="tel" placeholder="请输入二级密码">
            </div>
            <div class="item">
                <label for="area">新密码</label>
                <input v-model="newpin" id="area" type="text" placeholder="请输入原始密码">
            </div>
            <div class="item">
                <label for="addr">确认密码</label>
                <input v-model="repin" id="addr" type="text" placeholder="请输入新密码">
            </div>
            <div class="sub"><input @click.stop="savepin" class="btn" type="submit" value="修改"></div>
        <!-- </form> -->
        <shade v-if="shade" :warn="warn"></shade>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default {
    data(){
        return {
            msg: '修改密码',
            password:'',
            newpassword:'',
            repassword:'',
            pin:'',
            newpin:'',
            repin:'',
            shade: false,
            warn: ''
        }
    },
    methods: {
        savepw(){
            this.$http.get(this.getIp()+'/mobile.php/member/pass?password='+this.password+'&newpassword='+this.newpassword+'&repassword='+this.repassword)
                .then((res) => {
                    console.log(res);
                    console.log('登录密码修改成功');
                    if(res.body.code === 200)
                    {
                        // setTimeout(function(){
                        this.warn = '登录密码修改成功'    
                        this.shade = true;
                        // },1000)
                    }else if(res.body.code === 205)  
                    {
                        this.warn = '亲，您未登录！！！'
                        this.shade = true;
                    }else if(res.body.code === 300)
                    {
                        this.warn = '缺少参数！！！'
                        this.shade = true;
                    }
            })            
        },
        savepin(){
            this.$http.get(this.getIp()+'/mobile.php/member/pin?pin='+this.pin+'&newpin='+this.newpin+'&repin='+this.repin)
                .then((res) => {
                    console.log(res);
                    console.log('二级密码修改成功');
                    if(res.body.code === 200)
                    {
                        // setTimeout(function(){
                        this.warn = '二级密码修改成功'    
                        this.shade = true;
                        this.$router.go(-1);
                        // },1000)
                    }else if(res.body.code === 205)  
                    {
                        this.warn = '亲，您未登录！！！'
                        this.shade = true;
                    }else if(res.body.code === 300)
                    {
                        this.warn = '缺少参数！！！'
                        this.shade = true;
                    }
            })            
        }
    },
    created() {
    },
    components: {
        'v-back':back,
        shade
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#pw {
    padding-top: 50px;
    form {
        border-radius: 6px;
        overflow: hidden;
    }
    .tit {
        padding: 10px;
        border-bottom: 1px solid #9e0028;
        color: #9e0028;
        margin-bottom: 10px;
    }
    .item {
        background-color: #fff;
        position: relative;
        border-bottom: 1px solid #ddd;
        label {
            position: absolute;
            width: 70px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 75%;
            margin-left: 80px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .sub {
        text-align: center;
        background-color: #fff;
        padding: 10px;
        .btn {
            background-color: #9e0028;
            padding: 4px 30px;
            border: none;
            color: #fff;
            font-weight: 700;
            border-radius: 6px;
        }

    }
}
</style>



