<style scoped lang="less" rel="stylesheet">
// 头部
.mint-header {
    background-color: #9e0028;
    height: 50px;
    font-size: 18px;
}

// 底部
.mui-bar-tab {
    background-color: #fff;
}
.mui-bar-tab .mui-tab-item.mui-active {
    color: #9e0028;
}
.mui-bar-tab .mui-tab-item {
    height: 60px;
    color: black;
    font-size: 14px;
}
//图标
.icon {
    display: inline-block;
    height: 30px;
    img {
        width: 40px;
        height: 30px;
    }
}
</style>



