<template>
    <div id="car">
        <headerfooter></headerfooter>

        <!-- 选中商品 -->
        <div v-for="item in car" v-if="car.length > 0" class="item">
            <div class="box">
                <div class="box-tit">
                    <span class="checked"></span>
                    <div class="text">{{item.title}}</div>
                    <!-- <span class="more"></span> -->
                </div>
                <div class="product">
                    <div class="img"><img :src="item.picture" alt=""></div>
                    <div class="describe">
                        <div class="tit">{{item.short_title}}</div>
                        <div class="price">{{item.product_price | price}}</div>
                        <!-- <div class="worder">工费：￥48.00</div> -->
                        <!-- <del class="del">￥{{item.product_price}}</del> -->
                    </div>
                </div>
                <div class="box-footer">
                    <span class="icon"> 惠</span>
                    <span @click.stop="del(item.cid)" class="del"></span>
                </div>
            </div>
        </div>
        <!-- 清空商品 -->
        <div v-if="car.length > 0" class="remove">
            <span @click.stop="delall" class="delgoods">清 空</span>
        </div>
        <!-- 支付 -->
        <div v-if="car.length > 0" class="shopcart">
            <div class="total">合计（免运费）<span class="tot-price">{{totalPrice | price}}</span> </div>
            <div class="pay">
                <button @click.stop="pay(allId,totalPrice)" class="pay-btn">去支付<span>({{car.length}})</span></button>
            </div>
        </div>
        <div v-if="car.length == 0" class="warn">亲，您的购物车是空的！！！</div>   
    </div>
</template>

<script>
import headerfooter from '@/components/header/header.vue';
export default{
    data()
    {
        return {
            car: []
        }
    }
    ,
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/cart/index')
            .then((res) => {
                console.log(res);
                   this.car = res.body.data;
                // console.log(this.car);
        })

    },
    methods:{
        pay(id,total){
            this.$http.get(this.getIp()+'/mobile.php/cart/addall?pid='+id+'&total='+total)
            .then((res) => {
                console.log(res);
                console.log('支付成功');
                // this.$router.go(0);
                //    this.car = res.body.data;
                // console.log(this.car);
            })        
        },
        delall(){
            this.$http.get(this.getIp()+'/mobile.php/cart/del?cid=all')
            .then((res) => {
                console.log(res);
                console.log("清空全部商品成功");
                this.$router.go(0);
                //    this.car = res.body.data;
                // console.log(this.car);
            })        
        },
        del(id){
            this.$http.get(this.getIp()+'/mobile.php/cart/del?cid='+id)
            .then((res) => {
                console.log(res);
                console.log("清空单个商品成功");
                this.$router.go(0);
                //    this.car = res.body.data;
                // console.log(this.car);
            })        
        }

    },
    computed: {
        totalPrice(){
                let total = 0;
                this.car.forEach((item)=>{
                    total += Math.floor(item.product_price);
                })
                // console.log(total);
                return total >0 ? total : 0;
        },
        allId(){
            let arr = [];
            this.car.forEach((item)=>{
                arr.push(item.id);
            })
            console.log(arr);
            return arr;
        }
    },
    components: {
        headerfooter
    }
}

</script>

<style scoped lang="less" rel="stylesheet/less">
#car {
    padding: 70px 10px 170px;
    font-size: 14px;
    .item {
        border: 1px solid #ddd;
        border-radius: 5px;
        margin-bottom: 10px;
        background-color: #fff;
        .box {
            .box-tit {
                padding: 4px 24px 4px 28px;
                position: relative;
                
                .checked {
                    position: absolute;
                    left: 6px;
                    top: 6px;
                    width: 14px;
                    height: 14px;
                    background: url(../../../static/images/chk1.png) no-repeat 0 0;
                    background-size: 14px 14px;
                }
                .text {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                }
                .more {
                    position: absolute;
                    right: 8px;
                    top: 6px;
                    width: 15px;
                    height: 15px;
                    background: url(../../../static/images/xg.png) no-repeat 0 0;
                    background-size: 15px 15px;
                }
            }
            .product {
                overflow: hidden;
                padding: 10px 6px;
                border-top: 1px solid #dddddd;
                border-bottom: 1px dashed #dddddd;
                .img {
                    float: left;
                    width: 70px;
                    height: 70px;
                    border-radius: 3px;
                    // border: 1px solid #9e0028;
                    margin-right: 10px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                .describe {
                    line-height: 18px;
                    .tit {
                        font-size: 16px;
                        margin-bottom: 30px;
                    }
                    .price {
                        color: #9e0028;
                        font-size: 14px;
                    }
                    .worder {

                    }
                    .del {
                        font-size: 12px;
                        color: #666;
                    }
                }
            }
            .box-footer {
                overflow: hidden;
                padding: 5px 8px;
                .icon {
                    background: #3d83dc;
                    float: left;
                    padding: 0px 5px;
                    border-radius: 3px;
                    font-size: 8px;
                    color: #fff;
                }
                .del {
                    float: right;
                    width: 30px;
                    height: 15px;
                    background: url(../../../static/images/del.png) no-repeat 0 0;
                    background-size: 12px 15px;
                    margin-top: 3px;
                }
            }
        }
    }
    .remove {
        text-align: right;
        padding-right: 20px;
        padding-top: 20px;
        color: #00a0e9;
    }
    .shopcart {
        position: fixed;
        width: 100%;
        height: 50px;
        line-height: 50px;
        bottom: 60px;
        left: 0;
        background-color: #f8f8f8;
        padding-left: 14px;
        font-size: 14px;
        border-top: 1px solid #ddd;
        .total {
            float: left;
            .tot-price {
                color: #9e0028;
            }
        }
        .pay {
            float: right;
            margin: 10px 10px;
            .pay-btn {
                background-color: #9e0028;
                color: #fff;

            }
        }
    }
    .warn {
        text-align: center;
        color: #9e0028;
    }
}

</style>


