<template>
    <div id="detail">
        <v-back :msg="msg"></v-back>
        <!-- <form action="#"> -->
            <!-- <div class="item">
                <label for="username">用户名</label>
                <input v-model="username" id="username" type="text" placeholder="请输入联系人姓名">
            </div> -->
            <div class="item">
                <label for="tel">手机号</label>
                <input v-model="phone" id="tel" type="tel" placeholder="大于7位的电话号码" disabled>
            </div>
            <div class="item">
                <label for="area">真实姓名</label>
                <input v-model='name' id="area" type="text" placeholder="请输入真实姓名">
            </div>
            <!-- <div class="item">
                <label for="alipay">支付宝</label>
                <input v-model="alipay" id="alipay" type="text" placeholder="请输入支付宝账号">
            </div> -->
            <div class="item">
                <label for="wechat">微信号</label>
                <input v-model = "wechat" id="wechat" type="text" placeholder="请输入微信号">
            </div>
            <div class="item">
                <label for="coding">支付密码</label>
                <input v-model="pin" id="coding" type="text" placeholder="请输入支付密码">
            </div>
            <input @click.stop="revise" class="btn" type="submit" value="保存">
        <!-- </form> -->
        <shade v-if="shade" :warn="warn"></shade>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default {
    data(){
        return {
            msg: '修改资料',
            user: {},
            username: '',
            phone:'',
            name: '',
            alipay:'',
            wechat: '',
            pin: '',
            shade: false,
            warn: ''
        }
    },
    methods: {
        revise(){
            this.$http.get(this.getIp()+'/mobile.php/member/update?phone='+this.phone+'&name='+this.name+'&alipay='+this.alipay+'&wechat='+this.wechat+'&pin='+this.pin)
            .then((res) => {
                console.log(res);
                this.user = res.body.data;
                this.phone = this.user.phone;
                this.name = this.user.name;
                this.alipay = this.user.alipay;
                this.wechat = this.user.wechat;
                this.pin = this.user.pin;
                // this.$router.go(-1);
                console.log('资料保存成功');
                if(res.body.code === 200)
                {
                    // setTimeout(function(){
                    this.warn = '资料保存成功'    
                    this.shade = true;
                    // },1000)
                }else if(res.body.code === 205)  
                {
                    this.warn = '亲，您未登录！！！'
                    this.shade = true;
                }else if(res.body.code === 300)
                {
                    this.warn = '缺少参数！！！'
                    this.shade = true;
                }
                // console.log(this.user);
            })        
        }
    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/member/edit')
            .then((res) => {
                console.log(res);
                this.user = res.body.data;
                this.phone = this.user.phone;
                this.name = this.user.name;
                this.alipay = this.user.alipay;
                this.wechat = this.user.wechat;
                this.pin = this.user.pin;
                // console.log(this.user);
        })
    },
    components: {
        'v-back':back,
        shade
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#detail {
    padding-top: 60px;
    .item {
        background-color: #fff;
        position: relative;
        border-top: 1px solid #ddd;
        label {
            position: absolute;
            width: 70px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 75%;
            margin-left: 80px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .btn {
        position: fixed;
        right: 4px;
        top: 8px;
        background-color: transparent;
        border: none;
        color: #fff;
        font-weight: 700;
        z-index: 100;
    }
}
</style>



