<template>
    <div id="thaw">
        <v-back :msg="msg"></v-back>
        <div class="list">
            <ul>
                <li>
                    <span class="uname">购买</span>
                    <span class="tname">消费金额</span>
                    <span class="status">时间</span>
                </li>
                <li v-for="item in data">
                    <span class="uname">{{item.type}}</span>
                    <span class="tname">{{item.recash_expend}}</span>
                    <span class="status">{{Math.floor(item.recash_time) | data}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg: '复投记录',
            data: []
        }
    },
    methods: {

    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/Point/recash')
            .then((res) => {
                 console.log(res);
                 this.data = res.body.data;
                // this.slide = res.body.data.picture;
                // this.notice = res.body.data.notice;
                // console.log(this.notice);
        })
    },
    components: {
        'v-back':back
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#thaw {
    // position: absolute;
    // width: 100%;
    // height: 100%;
    // left: 0;
    // top: 50px;
    // z-index: 29;
    // background-color: #efeff4;
    padding-top: 50px;
    .list {
        padding-top: 20px;
        ul {
            background-color: #fff;
            border-radius: 6px;
            li {
                overflow: hidden;
                line-height: 40px;
                background-color: #E5E5E5;
                border-bottom: 1px solid #ddd;
                &:first-of-type {
                    background-color: #F1F1F1;
                    span {
                        font-weight: 700;
                    }
                }
                span {
                    float: left;
                    text-align: center;
                    color: #666;
                    font-size: 10px;
                    height: 40px;
                    &.uname {
                        width: 30%;
                    }
                    &.tname {
                        width: 20%;
                    }
                    &.status {
                        width: 50%;
                    }
                }
            }
        }
    }

}
</style>



