<template>
    <div id="attorn">
        <v-back :msg="msg"></v-back>
        <div class="tit">转让前确认您已到对方款项</div>
        <!-- <form action="#"> -->
            <div class="item">
                <label for="username">对方账号</label>
                <input v-model="username" id="username" type="text" placeholder="请输入对方账号">
            </div>
            <div class="item">
                <label for="tel">转让数量</label>
                <input v-model="number" id="tel" type="tel" placeholder="请输入转让数量">
            </div>
            <div class="item">
                <label for="area">二级密码</label>
                <input v-model="pin" id="area" type="text" placeholder="请输入二级密码">
            </div>
            <div class="item">
                <label for="addr">当前剩余</label>
                <input v-model="a" id="addr" type="text" placeholder="当前剩余" disabled>
            </div>
            <div class="sub"><input @click.stop="attorn" class="btn" type="submit" value="转让"></div>
        <!-- </form> -->
        <div class="list">
            <ul>
                <li>
                    <span class="uname">对方用户</span>
                    <span class="tname">转让数量</span>
                    <span class="status">转让时间</span>
                </li>
                <li v-for="item in data">
                    <span class="uname">{{item.username}}</span>
                    <span class="tname">{{item.number}}</span>
                    <span class="status">{{Math.floor(item.time) | data}}</span>
                </li>
            </ul>
        </div>

        <v-shade v-show="shade" :warn='warn'></v-shade>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
import shade from '@/components/shade/shade.vue';
export default {
    data(){
        return {
            msg: '激活码转让',
            username: '',
            number: '',
            pin: '',
            data:[],
            a:'',
            shade: false,
            warn: ''
        }
    },
    methods: {
        attorn (){
            this.$http.get(this.getIp()+'/mobile.php/Code/click?username='+this.username+'&number='+this.number+'&pin='+this.pin)
            .then((res) => {
                console.log(res);
                console.log('转让成功');
                // this.data = res.body.data;
                // console.log(this.data);
                if(res.body.code === 200)
                {
                    // setTimeout(function(){
                    this.warn = '激活码转让成功'    
                    this.shade = true;
                    // },1000)
                }else if(res.body.code === 205)  
                {
                    this.warn = '亲，您未登录！！！'
                    this.shade = true;
                }else if(res.body.code === 300)
                {
                    this.warn = '缺少参数！！！'
                    this.shade = true;
                }
            })
            
            // this.shade = true;
        }
    },
    created()
    {
        this.$nextTick(function(){
            this.$http.get(this.getIp()+'/mobile.php/Code/attorn')
            .then((res) => {
                console.log(res);
                this.data = res.body.data;
                console.log(this.data);
                if(this.data.length>0){
                    this.a = this.data[0].code_no;
                }
            })
        })    
    },
    components: {
        'v-back':back,
        'v-shade':shade
    }
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#attorn {
    padding-top: 50px;
    form {
        border-radius: 6px;
        overflow: hidden;
    }
    .tit {
        padding: 10px;
        border-bottom: 1px solid #9e0028;
        color: #9e0028;
        margin-bottom: 10px;
    }
    .item {
        background-color: #fff;
        position: relative;
        border-bottom: 1px solid #ddd;
        label {
            position: absolute;
            width: 110px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 60%;
            margin-left: 120px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .sub {
        text-align: center;
        background-color: #fff;
        padding: 10px;
        .btn {
            background-color: #9e0028;
            padding: 4px 30px;
            border: none;
            color: #fff;
            font-weight: 700;
            border-radius: 6px;
        }

    }
    .list {
        padding-top: 20px;
        ul {
            background-color: #fff;
            border-radius: 6px;
            li {
                overflow: hidden;
                line-height: 40px;
                background-color: #E5E5E5;
                border-bottom: 1px solid #ddd;
                &:first-of-type {
                    background-color: #F1F1F1;
                    span {
                        font-weight: 700;
                    }
                }
                span {
                    float: left;
                    text-align: center;
                    color: #666;
                    font-size: 10px;
                    height: 40px;
                    &.uname {
                        width: 30%;
                    }
                    &.tname {
                        width: 30%;
                    }
                    &.status {
                        width: 40%;
                    }
                }
            }
        }
    }

}
</style>



