<template>
    <div id="auction">
        <v-back :msg='msg'></v-back>
        <div class="integral">
            <div class="week-box">
                <div class="week">本周可获得1分</div>
                <div class="score">2</div>
            </div>
            <div class="btn">
                <mt-button type="danger">积分({{data.point}})</mt-button>
                <mt-button type="danger">已解冻积分({{data.unfreeze}})</mt-button>
            </div>
            <div @click="show = !show" class="widthdraw">
                提现
            </div>
        </div>
        <div class="my-order">
            <div class="order-top">
                <router-link class="link" to="/home/auction/integral">
                    <span class="order-text">积分记录</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
            <div class="order-top">
                <router-link class="link" to="/home/auction/widthdraw">
                    <span class="order-text">提现记录</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
            <div class="order-top">
                <router-link class="link" to="/home/auction/thaw">
                    <span class="order-text">复投记录</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
        </div>
        <draw v-if="show"></draw>
    </div>
</template>
<script>
import back from '@/components/back/back.vue';
import draw from '@/components/auction/draw.vue';

export default {
    data(){
        return {
            msg: '积分',
            data: {},
            show: false
        }
    },
    methods: {

    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/Point/index')
            .then((res) => {
                 console.log(res);
                 this.data = res.body.data;
                // this.slide = res.body.data.picture;
        })
    },  
    components:{
        'v-back': back,
        draw
    }
}
</script>
<style scoped lang="less" rel="stylesheet">

#auction {
    // position: absolute;
    // left: 0;
    // top: 50px;
    // z-index: 33;
    padding-top: 50px;
    // width: 100%;
    // height: 100%;
    // background-color: #fff;
    .integral {
        padding:20px;
        position: relative;
        .week-box {
            width: 150px;
            height: 150px;
            border: 6px solid #9e0028;
            border-radius: 50%;
            margin: 0 auto;
            padding: 20px;
            text-align: center;
            .week {
                font-size: 10px;
                color: #9e0028;
            }
            .score {
                font-size: 20px;
                color: #9e0028;
                border-bottom: 1px solid #9e0028;
                line-height: 30px; 
            }
        }
        .btn {
            padding-top: 20px;
            overflow: hidden;
            .mint-button {
                width: 48%;
                &:first-of-type {
                    float: left;
                    background-color: #6BB941;
                }
                &:last-of-type{
                    float: right;
                    background-color: #5CB6D0;
                }
            }
        }
        .widthdraw {
            position: absolute;
            right: 20px;
            top: 20px;
            color: #DB4358;
        }

    }
    .my-order {
        margin: 10px 0;
        background-color: #fff;
        border-top: 1px solid #ddd;
        .order-top {
            padding: 10px;
            overflow: hidden;
            border-bottom: 1px solid #ddd;
            .link {
                color: #666;
            }
            .order-text {
                float: left;
            }
            .arrrow {
                float: right;
            }
        }
    }
}
</style>



