<template>
    <div id="myorder">
        <v-back :msg="msg"></v-back>

        <div class="nav">
            <ul>
                <li><router-link class="active" to="/my/myorder/money">待付款</router-link></li>
                <li><router-link class="active" to="/my/myorder/goods">待收货</router-link></li>
                <li><router-link class="active" to="/my/myorder/comment">已收货</router-link></li>
            </ul>
        </div>

        <router-view :goodsList='goodsList' :moneyList='moneyList' :commentList='commentList'></router-view>

        <div class="back">
            <router-link class="link" to="/my">我的</router-link>
        </div>

    </div>
</template>
<script>
import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg: '订单管理',
            goodsList: [],
            moneyList: [],
            commentList: []
        }
    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/order/index?handle_type=0')
            .then((res) => {
                console.log(res);
                this.moneyList = res.body.data;
                console.log(this.moneyList);
        })
        this.$http.get(this.getIp()+'/mobile.php/order/index?handle_type=1')
            .then((res) => {
                console.log(res);
                this.goodsList = res.body.data;
                // console.log(this.data);
        })
        this.$http.get(this.getIp()+'/mobile.php/order/index?handle_type=2')
            .then((res) => {
                console.log(res);
                this.commentList = res.body.data;
                // console.log(this.data);
        })
    },
    methods: {
    },
    components: {
        'v-back': back
    }
}
</script>
<style scoped lang="less" rel="stylesheet">
#myorder {
    padding-top: 100px;
    .box {
        margin-top: 10px;
        .item {
            .num {
                overflow: hidden;
                height: 40px;
                line-height: 40px;
                padding: 0 10px;
                border-top: 1px solid #ccc;
                border-bottom: 1px solid #ccc;
                .left {
                    float: left;
                    font-size: 14px;
                }
                .right {
                    float: right;
                    color: #259B23;
                    font-size: 14px;
                }
            }
            .product {
                border-bottom: 1px solid #ccc;
                .img-text {
                    padding: 10px;
                    overflow: hidden;
                    img {
                        float: left;
                        width: 80px;
                        height: 80px;
                        margin-right: 10px;
                    }
                    .content {
                        padding-left: 10px;
                        position: relative;
                        .text {
                            font-size: 14px;
                            
                        }
                        .total {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            color: #666;
                        }
                    }
                }
            }
            .btn {
                text-align: right;
                padding: 10px;
                button {
                    border-radius: 8px;
                    border-color: #333;
                    &:last-of-type {
                        border-color: #259B23;
                        color: #259B23;
                        margin-left: 10px;
                    }
                }
            }
        }
    }

    .nav {
        position: fixed;
        top: 50px;
        left: 0;
        width: 100%;
        background-color: #F8F8F8;
        z-index: 33;
        ul {
            overflow: hidden;
            line-height: 50px;
            border-bottom: 1px solid #ccc;
            li {
                float: left;
                width: 33.33%;
                text-align: center;
                position: relative;
                .active {
                    color: #666;
                }
                .mui-active {
                    color: #9e0028;
                }
                .mui-active::before {
                    content: '';
                    width: 100%;
                    height: 2px;
                    position: absolute;
                    bottom: 0px;
                    left: 0;
                    z-index: 2;
                    background-color: #9e0028;
                }
            }
        }
    }
    .back {
        position: fixed;
        right: 14px;
        top: 14px;
        z-index: 99;
        .link {
            color: #fff;
        }
    }
}
</style>



