<template>
    <div id="activate">

        <headerfooter></headerfooter>

        <!-- 顶部图片 -->
        <div class="header">
            <div class="img"><img src="../../../static/images/activate_1.jpg" alt=""></div>
            <div class="love"><img src="../../../static/images/activate_2.png" alt=""></div>
            <div class="text">D·S·Y 新型互联消费</div>
        </div>
        <!-- 每一项 -->
        <div class="my-order">
            <div class="order-top">
                <router-link class="link" to="/activate/shop">
                    <span class="order-text">激活码购买</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
            <div class="order-top">
                <router-link class="link" to="/activate/attorn">
                    <span class="order-text">激活码转让</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
            <div class="order-top">
                <router-link class="link" to="/activate/activation">
                    <span class="order-text">会员激活</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import headerfooter from '@/components/header/header.vue';
export default{
    data()
    {
        return {
            data:{}
        }
    }
    ,
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/Code/attorn')
            .then((res) => {
                console.log(res);
                this.data = res.body.data;
                // console.log(this.data);
        })
    }
    ,
    methods:{  

    },
    components: {
        headerfooter
    }
}

</script>

<style scoped lang="less" rel="stylesheet/less">
#activate {
    padding: 50px 0 70px;
    .header {
        position: relative;
        .img {
            height: 200px;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .love {
            height: 30px;
            position: absolute;
            left: 0;
            bottom: 0;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .text {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 6px;
            color: rgb(249, 127, 44);
        }
    }
    .my-order {
        margin: 10px 0;
        background-color: #fff;
        .order-top {
            padding: 10px;
            overflow: hidden;
            border-bottom: 1px solid #ddd;
            .link {
                color: #666;
            }
            .order-text {
                float: left;
            }
            .arrrow {
                float: right;
            }
        }
    }
}
</style>


