<template>
    <div id="recommend">
        <v-back :msg='msg'></v-back>
        <div class="my-order">
            <div class="order-top">
                <router-link class="link" to="/home/recommend/register">
                    <span class="order-text">添加会员</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
            <div class="order-top">
                <router-link class="link" to="/home/recommend/user">
                    <span class="order-text">直推用户</span>
                    <span class="arrrow"> > </span>
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
import back from '@/components/back/back.vue'
export default {
    data(){
        return {
            msg: '直推'
        }
    },
    methods: {

    },
    components:{
        'v-back': back
    }
}
</script>
<style scoped lang="less" rel="stylesheet">

#recommend {
    // position: absolute;
    // left: 0;
    // top: 50px;
    // z-index: 33;
    // width: 100%;
    // height: 100%;
    // background-color: #fff;
    padding-top: 50px;
    .my-order {
        margin: 10px 0;
        background-color: #fff;
        border-top: 1px solid #ddd;
        .link {
            color: #222;
        }
        .order-top {
            padding: 10px;
            overflow: hidden;
            border-bottom: 1px solid #ddd;
            .order-text {
                float: left;
            }
            .arrrow {
                float: right;
            }
        }
    }
}
</style>



